<template>
  <div>
    <vue-html2pdf
      :show-layout="false"
      :float-layout="true"
      :enable-download="false"
      :preview-modal="true"
      :paginate-elements-by-height="1400"
      :filename="`getExonerado`"
      :pdf-quality="2"
      :manual-pagination="false"
      pdf-format="a4"
      pdf-orientation="portrait"
      pdf-content-width="800px"
      ref="html2Pdf"
      class="h-full m-0 p-0"
      @change="close"
    >
      <section
        slot="pdf-content"
        class="m-0 p-14 text-black text-sm font-medium"
      >
        <!-- PDF Content Here -->

        <section class="flex justify-between items-center m-0">
          <div class="w-24">
            <img src="@//assets//images//unap.jpg" alt />
          </div>

          <div class="w-48 text-right uppercase">
            <span>dirección de admisión</span>
          </div>
        </section>

        <section class="mt-10">
          <div class="text-center">
            <h1 class="text-4xl font-bold">Examen de Admisión {{domain_object.name}}</h1>
            <span class="text-2xl">HOJA DE PRE-REGISTRO</span>
          </div>

          <div class="text-2xl mt-10 font-bold uppercase">
            <span>Modalidad: {{ item_selected.modalidad_text }}</span>
          </div>
        </section>

        <section class="mt-5 uppercase flex flex-col text-lg">
          <div class="flex flex-col mt-10">
            <span>NOMBRE: {{ item_selected.nombre }}</span>
            <span>
              Dni:
              <b>{{ item_selected.num_doc }}</b>
            </span>
            <span>
              número de operación:
              <b>{{ item_selected.num_operacion }}</b>
            </span>
            <span>
              fecha:
              <b>{{ parseDate(item_selected.fecha) }}</b>
            </span>

            <span class="mt-10 font-bold"
              >Ref. {{ item_selected.documento_referencial }}</span
            >

            <div class="flex justify-between mt-10">
              <div></div>

              <span>sistemas DA-unap</span>
            </div>

            <div>
              <h1 class="text-xl font-bold mb-2">ATENCIÓN:</h1>
              <p>
                Después de haber recibido la inscripción ingrese a:
                <span class="lowercase underline text-blue-500"
                  >http://admision.unapiquitos.edu.pe/</span
                >
                y clic en la opción "Inscríbase Aquí".
              </p>
            </div>
          </div>
        </section>
      </section>
    </vue-html2pdf>
  </div>
</template>

<script>
// import VueHtml2pdf from "vue-html2pdf";

const VueHtml2pdf = () => import("vue-html2pdf");

export default {
  props: {
    item_selected: {
      type: Object,
    },
  },

  components: {
    VueHtml2pdf,
  },

  computed: {
    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },

    domain_object() {
      return this.$store.getters["tools/get_domain"];
    },
  },

  watch: {},

  mounted() {
    setTimeout(() => {
      this.generateReport();
    }, 500);
  },

  destroyed() {
    this.$emit("hasgenerate");
  },

  methods: {
    parseDate(val) {
      try {
        const date = val.split("-");
        let anio, mes, dia;
        anio = date[0];
        mes = date[1];
        dia = date[2];
        return `${dia}-${mes}-${anio}`;
      } catch (error) {
        return "";
      }
    },

    close() {
      this.$emit("hasgenerate");
    },

    generateReport() {
      this.$refs.html2Pdf.generatePdf();
    },

    completar00(val) {
      if (val.toString().length < 10) {
        let final = 10 - val.toString().length;
        let result = "";
        for (let index = 0; index < final; index++) {
          result += "0";
        }
        result += val.toString();
        return result;
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
