<template>
  <div>
    Generando Reporte...
    <reporte-exonerado-print
      v-if="show"
      :item_selected="item_selected"
      @hasgenerate="hasgenerate"
    />
  </div>
</template>

<script>
import ReporteExoneradoPrint from "./components/ReporteExoneradoPrint.vue";
export default {
  components: { ReporteExoneradoPrint },

  data() {
    return {
      item_selected: {},
      show: false,
    };
  },

  methods: {
    hasgenerate() {
      console.log("murio");
    },

    async set_postulante() {
      await this.$store
        .dispatch("pago/set_pago_banco_by_id", this.$route.params.id)
        .then((resp) => {
          this.item_selected = resp;
        });
    },
  },

  async mounted() {
    // await this.$store.dispatch("auth/set_user");
    await this.set_postulante();
    this.show = true;
  },
};
</script>

